.overlay {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 5;
  display: none;
}

.search {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 42px;
  transform: translateX(100%);
  transition: transform .5s;
  z-index: 6;
}

.search.is-open {
  transform: translateX(0);
}

@media only screen and (max-width: 1023px) {
  .search {
    display: block;
  }
}

.search__label {
  display: block;
  position: relative;
}

.search__input {
  width: 100%;
  height: 42px;
  padding: 10px 62px 10px 15px;
  border: 1px solid #d2d2d2;
  box-sizing: border-box;
  font-size: 14px;
  font-weight: 700;
  color: #000000;
}

.search__input::placeholder {
  font-size: 14px;
  font-weight: 700;
  color: #9b9b9b;
}

.search__btn-focus {
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  width: 42px;
  height: 42px;
  background: none;
  background-color: transparent;
  cursor: pointer;
}

.search__btn-focus .icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 19px;
  height: 20px;
  fill: #000000;
  transition: fill .3s;
}

.btn-search {
  position: relative;
  width: 42px;
  height: 42px;
  background: none;
  background-color: #e7462e;
  z-index: 3;
}

.btn-search.is-open {
  background-color: transparent;
  transition-delay: .5s;
  z-index: -1;
}

.btn-search:hover .icon {
  fill: #000000;
}

.btn-search .icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 19px;
  height: 20px;
  fill: #ffffff;
  transition: fill .3s;
}

.toggle {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: none;
  cursor: pointer;
}

.toggle__line {
  display: block;
  width: 20px;
  height: 2px;
  background-color: #fff;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
  transition: .5s;
}

.toggle__line:not(:last-child) {
  margin-bottom: 5px;
}

.toggle.is-active .toggle__line:nth-child(2) {
  transform: translateX(-50px);
  opacity: 0;
}

.toggle.is-active .toggle__line:nth-child(1) {
  transform: rotateY(180deg) rotateZ(45deg) translate(5px, 5px);
}

.toggle.is-active .toggle__line:nth-child(3) {
  transform: rotateY(180deg) rotateZ(-45deg) translate(5px, -5px);
}

