}

.article__text {
  grid-column-start: 2;
  grid-column-end: 4;
  line-height: 1.7;
  font-size: 15px;
  color: #646464;
  margin-left: 30px;
}

@media only screen and (max-width: 1150px) {
  .article__text {
    grid-row-start: 3;
    grid-row-end: 4;
    grid-column-start: 1;
    grid-column-end: 4;
  }
}

@media only screen and (max-width: 1023px) {
  .article__text {
    margin-left: 10px;
    font-size: 14px;
  }
}

@media only screen and (max-width: 767px) {
  .article__text {
    grid-row-start: 4;
    grid-column-start: 1;
    margin-left: 0;
  }
}

.counter {
  display: flex;
  font-size: 15px;
  color: #a6a6a6;
  margin-bottom: 12px;
}

@media only screen and (max-width: 1023px) {
  .counter {
    font-size: 12px;
    margin-bottom: 6px;
  }
}

.counter__date {
  margin-right: 38px;
}

.counter__views-icon .icon {
  width: 19px;
  height: 11px;
  fill: #e7462e;
}

@media only screen and (max-width: 1023px) {
  .counter .icon {
    width: 15px;
    height: 9px;
  }
}

.page-nav {
  margin: 43px 0;
}

@media only screen and (max-width: 1023px) {
  .page-nav {
    margin: 20px 0;
  }
}

.page-nav__list {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

@media only screen and (max-width: 767px) {
  .page-nav__list {
    justify-content: center;
  }
}

@media only screen and (max-width: 767px) {
  .page-nav__item {
    display: none;
  }
  .page-nav__item:first-child, .page-nav__item:last-child, .page-nav__item:nth-child(2), .page-nav__item:nth-child(3) {
    display: block;
  }
}

.page-nav__item:not(:last-child) {
  margin-right: 8px;
}

.page-nav__item.is-active .page-nav__link {
  color: #ffffff;
  background-color: #472d33;
}

.page-nav__btn, .page-nav__link {
  background: none;
  outline: none;
  border: none;
  width: 40px;
  height: 40px;
  background-color: transparent;
  font-size: 15px;
  font-weight: 700;
  color: #1d1d1d;
  transition: background-color .3s, color .3s;
  text-align: center;
}

.page-nav__link:hover {
  background-color: #472d33;
  color: #ffffff;
}

.page-nav__btn {
  position: relative;
  margin-left: 37px;
  font-size: 0;
}

.page-nav__btn:disabled {
  cursor: default;
}

.page-nav__btn:disabled .icon {
  opacity: .4;
}

.page-nav__btn:disabled:hover .page-nav__icon--mod {
  opacity: 1;
}

.page-nav__btn:hover .page-nav__icon--mod {
  opacity: 0;
}

.page-nav__btn .icon {
  width: 12px;
  height: 24px;
  fill: #e7462e;
}

.page-nav__icon--mod {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 1;
  transition: opacity .3s;
}

.page-nav__icon--mod .icon {
  width: 7px;
  height: 18px;
  fill: #ffffff;
}

.page-nav__btn--mod {
  margin-left: 0;
  margin-right: 37px;
  transform: rotate(180deg);
}
